* {
  box-sizing: border-box;
}

/*Style for Navbar component*/

.logo {
  color: rgb(255, 85, 0) !important;
  text-transform: uppercase;
  font-size: 1rem !important;
  font-weight: bold;
}

.subscribe-btn {
  font-size: 0.7rem !important;
  border: 1px solid rgb(255, 85, 0) !important;
  background: rgba(255, 85, 0, 0.1) !important;
  border-radius: 5px;
  padding: 10px;
}

.sign-in {
  font-size: 0.7rem !important;
  text-transform: uppercase;
  text-decoration: none;
  color: black;
  border: none;
}

.sign {
  color: black !important;
}

.active {
  color: black !important;
}

.inactive {
  color: grey !important;
}

/*Style for Article component*/
.article-image {
  position: relative;
  background-image: url("clothes.jpg"); /* The image used */
  background-color: #cccccc; /* Used if the image is unavailable */
  height: 300px; /*You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
}

.hero {
  position: relative;
  background-image: url("clothes.jpg"); /* The image used */
  background-color: #cccccc; /* Used if the image is unavailable */
  height: 300px; /*You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
}

.boxBorder {
  border: solid rgba(255, 85, 0, 0.5) 5px;
  margin-bottom: 20px;
}

.bottom-left {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  color: rgb(255, 85, 0);
  background-color: rgb(255, 255, 255);
  padding: 10px;
  font-size: 1.5rem;
}

.info {
  background: rgba(255, 85, 0, 0.1) !important;
}

.functionname {
  font-size: 1.2rem;
}

.holder {
  position: relative;
  width: 200px;
  height: 50px;
}

.waveimg {
  background-image: url("articlewave.svg");
  height: 10px; /*You must set a specified height */
  width: 300px;
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  padding-bottom:10px;
}

.wavetwo {
  height: 15px; /*You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  transform: rotate(25deg);
  margin-left: -55px;
  margin-top: 48px;
  margin-bottom: 10px;
}

.explore {
  text-decoration: none;
  color: black;
}

.table>thead {
